body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
      "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
    
.App {
  background-color: rgb(156, 166, 214);
  min-height: 100vh;
}
* {
    box-sizing: border-box;

}

.user-list-container {
    width: 600px;
    margin: 0 auto;
    padding: 30px;
    text-align: center;
}
h1 {
    margin-top: 20px;
    color: rgb(255, 255, 255);
}

.input-user {
    height: 40px;
    width: 300px;
    margin: 5px;
    padding: 5px;
    font-size: 16px;
    border: none;
    border-radius: 2px;
}

.btn {
    height: 40px;
    margin: 5px;
    padding: 0 10px;
    font-size: 16px;
    color: rgb(255, 255, 255);
    background-color: rgb(31, 66, 161);
    border: none;
    border-radius: 2px;
    cursor: pointer;
}

.btn:hover {
    color: rgb(255, 255, 255);  
    background-color: rgb(143, 179, 111);
}

.btn:focus {
    outline: none;
}

ul.user-list {
    list-style: none;
    margin: 20px 0 0 0;
    padding: 0;
}

.user-list li {
    box-sizing: border-box;
    height: 50px;
    width: 300px;
    margin: 10px auto;
    line-height: 50px;
    color: white;
    background-color: rgb(68, 100, 189);
    font-size: 20px;
    /* border: 1px solid gray; */
    border-radius: 5px;
}

.user-list li:hover {
    background-color: rgb(148, 87, 87);
    border: 1px rgb(151, 40, 40) solid;
}
